import { createWebHistory, createRouter } from "vue-router";
import listKmsVue from "./components/listKms.vue";
import homeKms from './components/homeKms.vue';
import detailKms from './components/detailKms.vue';
import AuthorKms from './components/AuthorKms.vue';
import CommentKms from './components/CommentKms.vue';

const routes = [
  {
    path: "/list",
    component: listKmsVue,
  },
  {
    path: "/home",
    component: homeKms,
  },
  {
   //path: "/detail/:id(\\d+)",  //url파라미터문법숫자만 입력 vue 4
    path:"/detail/:id",  //여기서 ID는 작명이고 아무 문자나 들어오면,
    component:detailKms,
    children: [
        { path :"author", 
        component : AuthorKms
        },
        { path :"comment",
         component : CommentKms 
        },
      ]
  },
  {
    path: "/:anything(.*)",  //아무문자나 입력한 경우 홈으로 전송 404페이지
    component:homeKms,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,  // 라우터변수로 만들어서 셍성
});

export default router;//라우터 내보내기